.App #page-body {
  width: 50%;
  margin: 0 auto;
  padding: 1em 0;
}

.App nav {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 50px;
  border-bottom: 5px solid #000;
}

.App nav ul {
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  margin: 0;
  padding: 0;
}

.App nav ul li {
  display: flex;
  text-align: center;
  height: 50px;
}

.App nav ul li a {
  text-decoration: none;
  width: 100px;
  display: inline-block;
  padding: 0.8em 0;
}

.App nav ul a:hover {
  color: #fff;
  background-color: #000;
}
